import {initGlobalState, loadMicroApp, registerMicroApps, start} from "qiankun";

class Portal {
    /**
     * .
     */
    instancePortal (microApps, initialState) {
        const apps = initApps(microApps, initialState)
        registerMicroApps(apps, {
            beforeLoad: app => {
                console.log('before load app.name====>>>>>', app.name)
            },
            beforeMount: [
                app => {
                    console.log('[LifeCycle] before mount %c%s', 'color: green;', app.name)
                }
            ],
            afterMount: [
                app => {
                    console.log('[LifeCycle] after mount %c%s', 'color: green;', app.name)
                }
            ],
            afterUnmount: [
                app => {
                    console.log('[LifeCycle] after unmount %c%s', 'color: green;', app.name)
                }
            ]
        })

        start({sandbox: {experimentalStyleIsolation: true}})
    }

    /**
     * .
     * @param microApp .
     * @param initialState .
     * @return {MicroApp} .
     */
    loadMicro (microApp, initialState) {
        const app = {
            ...microApp,
            container: '#subapp-viewport',
            props: {
                entryBase: microApp.entry, // 
                globalState: initialState // getGlobalState
            }
        }
        const micro = loadMicroApp(app);
        return micro;
    }

    /**
     * portal.
     * @param store .
     * @param props .
     */
    loadGlobalState (store, props = {}) {
        if (!store || !store.hasModule) return
        // 
        const initState = { ...props.globalState }
        if (!store.hasModule('portal')) {
            const portalModule = {
                namespaced: true,
                state: initState,
                actions: {
                    // state
                    setPortalState({ commit }, payload) {
                        commit('setPortalState', payload)
                        commit('emitPortalState', payload)
                    },
                    // mount
                    initPortalState({ commit }, payload) {
                        commit('setPortalState', payload)
                    }
                },
                mutations: {
                    setPortalState(state, payload) {
                        state = Object.assign(state, payload)
                    },
                    emitPortalState(state) {
                        if (props.setGlobalState) {
                            props.setGlobalState(state)
                        }
                    }
                }
            }
            store.registerModule('portal', portalModule)
        }
    }

    globalState (initialState) {
        // 
        const actions = initGlobalState(initialState);
        return actions;
    }
}

/**
 *
 * @param microApps .
 * @param initialState .
 * @param $bus .
 */
function initApps (microApps, initialState) {
    const apps = microApps.map(item => {
        return {
            ...item,
            container: '#subapp-viewport', // div
            props: {
                entryBase: item.entry, // 
                globalState: initialState // getGlobalState
            }
        }
    })
    return apps
}

export { Portal };
